body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#landingFooter {
    background: url('https://www.toptal.com/designers/subtlepatterns/uploads/leaves.png') repeat top center;
    width: 100%;
}

#footerLink {
    text-decoration: none;
    color: #272727;
}

#footerLink:hover {
    color: #526faf;
}

#navWordmark {
    display: none;
}

#navLgLinks {
    display: none;
}

@media (min-width: 24rem) {
    #navLogo {
        display: none
    }
    
    #navWordmark {
        display: inline;
        display: initial;
    }

}

@media (min-width: 50rem) {
    #navSmLinks {
        display: none;
    }

    #navLgLinks {
        display: flex;
    }
}
.landingHeroImg {
    background: url(/static/media/mowing-hero.79e93cfc.jpg) no-repeat;
    background-size: cover;
    width: 100%;
}

.landingHeroIcon {
    width: 12rem;
    position: absolute;
    bottom: -10%;
    left: calc(50% - 6rem);
}
